import * as React from 'react';
import { styled } from '@mui/material/styles';

import IconButton from '@mui/material/IconButton';
import ProjectLanguages from "../../components/projectLanguages/ProjectLanguages";
// import { height } from '@mui/system';
// import { Helmet } from "react-helmet"
import "./materialCard.css";
import { Fade } from 'react-reveal';
// import sometihgn from "../../assests/images/gator-taxi.png"
const ExpandMore = styled((props) => {
  const { expand, ...other } = props;
  return <IconButton {...other} />;
})(({ theme, expand }) => ({
  transform: !expand ? 'rotate(0deg)' : 'rotate(180deg)',
  marginLeft: 'auto',
  height: 'auto',
  transition: theme.transitions.create('transform', {
    duration: theme.transitions.duration.shortest,
  }),
}));



export default function RecipeReviewCard({ repo, theme }) {
  
  const [isActive, setActive] = React.useState(null);
  const ToggleClass = () => {
    setActive(!isActive); 
   };

  // const handleExpandClick = () => {
  //   setExpanded(!expanded);
    
  //   console.log(expanded);
  // };
  // src/assests/images/gator-taxi.png
   const imagePath = `../../assests/images/gator-taxi.png`;
   console.log("image path"+imagePath);
  return (
    
    <div class="scene">
      {/* <Fade top duration={1000} distance = "20px" className="fader"> */}
      <div id="card_wrapper" className={isActive ? "is-flipped" : null} >
        <div class="card-face card-face--front">
          <div class="card-header-title">
              <p class="title-text">
                {repo.name}
              </p>
          </div>

          {/* <p class="card-subtitle">
            {repo.createdAt.split("T")[0]}
          </p> */}
          
          
          
          <img
            className="logo_img"
            src={require(`../../assests/images/${repo.imageDetails.imageName}`)}
            alt={imagePath}
          />
          <div class="short-text">
          <p >{repo.shortDescription}</p>
          </div>
          <div class="languages-know-more">
            <div class="repo-languages">
              <ProjectLanguages
                className="repo-languages"
                logos={repo.languages}
              />
            </div>
            <div class="know-more-button-div" >
              <button class="know-more-button" onClick={ToggleClass} >Know More</button>
            </div>
          </div>
        </div>
        <div class="card-face card-face--back">
          <p>Details:</p>
          <p> {repo.description}</p>
          <div class="know-more-button-div-back">
              <button class="know-more-button" onClick={ToggleClass} >Know Less</button>
            </div>
        </div>

      
      </div>
      {/* </Fade> */}
    </div>
  );
}


